* {
  box-sizing: border-box
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  margin: 4px;
}


.card {
    border-radius: 2px;
    height: 350px;
    margin: 1rem;
    margin-top: 10px;
    position: relative;
    width: 350px;
    box-shadow: 0 3px 6px rgb(19, 18, 18), 0 3px 6px #999;
    overflow: hidden;
}

.wrapper {
    padding-top: 10px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
}
.gameCount {
    line-height: 1em;
    height: 75px;
    position: relative;
    text-align: center;
    color: white;
    
}

h2.count{
    margin-top: 3px;
}


.App {
  text-align: center;
  background-color: #282c34
}

.App-header {
  background-color: #282c34;
  align-items: center;
  height: 90px;
  justify-content: center;
  color: white;
}

.App-intro {
  font-size: large;
}


